<template>
	<div class="feedback-container">
		<div class="feedback-header">
			<div class="feedback-zhong">
				<div class="feedback-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="feedback-ti">举报</div>
				<div></div>
			</div>
		</div>
		<div class="feedback-content">
			<!--<div class="feedback-biaoti">请填写联系方式（选填）</div>
			<div class="feedback-kuang">
				<div class="feedback-zi">
					<textarea></textarea>
				</div>
			</div>-->
			<div class="feedback-biaoti">请填写举报内容</div>
			<div class="feedback-kuang1">
				<div class="feedback-wenzi">
					<textarea v-model="content"></textarea>
				</div>
				<!--<div class="feedback-tianjia">
					<img src="../assets/btn_shangchuan.png">
				</div>-->
			</div>
			<!--<div class="feedback-flex">
				<div class="feedback-xuan">
					<img src="../assets/btn_niming.png">
				</div>
				<div class="feedback-tiwen">匿名提问</div>
			</div>-->
			<div class="feedback-tijiao" @click="report_discussion()">举报</div>
		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				userid: '',
				commentid: '',
				discussionid: '',
				content: '',
				user_info: {}
			}
		},
		methods: {
			back(){
				window.history.back();
			},
			report_discussion() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
					id: that.id,
					type: that.type,
					content: that.content,
					title: that.title
				};
				that.$http.post('/discussion/reportdis', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.discussion_list = res.data;
						that.content='',
						this.$toast.success('举报成功');
					}else if(res.meta.code == 201){
						this.$alert('你已举报过改信息', '提示');
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行
			this.id=this.$route.query.type,
			this.type=this.$route.query.typeone,
			this.title=this.$route.query.typetwo

		}
	}
</script>

<style scoped>
	.feedback-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.feedback-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		/*border-bottom: 1px solid #eaeaea;*/
	}
	
	.feedback-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.feedback-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.feedback-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.feedback-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
		background: #FFFFFF;
	}
	.feedback-biaoti{
		font-size: 1.4rem;
		color: #000000;
		padding: 1.7rem 1.5rem;
	}
	.feedback-kuang{
		border: 1px solid #f2f2f2;
		border-radius: 5px;
		background: #f2f2f2;
		height: 3rem;
		margin: 0 1.5rem;
	}
	.feedback-zi textarea{
		width: 100%;
		height: 3rem;
		padding: 0 1.5rem;
		background: #f2f2f2;
		border: none;
		padding-top: 0.5rem;
	}
	.feedback-wenzi textarea{
		width: 100%;
		height: 22rem;
		padding: 0 1.5rem;
		background: #f2f2f2;
		border: none;
		padding-top: 0.5rem;
	}
	.feedback-kuang1{
		border: 1px solid #f2f2f2;
		border-radius: 5px;
		background: #f2f2f2;
		height: 22rem;
		margin: 0 1.5rem;
	}
	/*.feedback-tianjia img{
		width: auto;
		height: 6rem;
		position: relative;
		top: -7rem;
		left: 0.5rem;
	}*/
	.feedback-flex{
		display: flex;
	}
	/*.feedback-xuan img{
		width: auto;
		height: 1.5rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}*/
	/*.feedback-tiwen{
		font-size: 1.5rem;
		color: #000000;
		padding: 0.9rem 0 1.6rem 0.5rem;
	}*/
	.feedback-tijiao{
		font-size: 1.8rem;
		border: 1px solid #FD5997;
		background: #FD5997;
		text-align: center;
		margin: 3rem 1.5rem;
		border-radius: 5px;
		padding: 0.5rem 0;
		color: #FFFFFF;
	}
</style>


